@import url("https://fonts.googleapis.com/css?family=Open+Sans");
/* 
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com 
Twitter: @rich_clark
*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

* {
  font-family: 'Open Sans';
}

header {
  border-bottom: 1px solid #ccc;
  padding-bottom: 50px;
  margin-top: 50px;
}

header h1, header h2 {
  text-align: center;
}

header h1 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

header h2 {
  font-size: 1.2em;
}

.formContainer {
  margin-top: 50px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.formContainer input {
  width: 300px;
  font-size: 1.2em;
  padding: 5px 10px;
  outline: none;
  border: 1px solid #ccc;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
}

.formContainer input:focus {
  border: 1px solid #666;
}

.formContainer button {
  border: none;
  background-color: royalblue;
  color: #fff;
  padding: 5px 15px;
  cursor: pointer;
}

.itemsContainer {
  width: 90%;
  margin: 20px auto;
  list-style: none;
}

.itemsContainer li {
  width: 70%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #ccc;
  padding: 5px 10px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
  position: relative;
  -webkit-box-shadow: 10px 10px 26px -18px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 26px -18px rgba(0, 0, 0, 0.75);
}

.itemsContainer li:hover {
  -webkit-box-shadow: 10px 10px 26px -15px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 26px -15px rgba(0, 0, 0, 0.75);
}

.itemsContainer li input {
  height: 20px;
  width: 20px;
}

.itemsContainer li label {
  display: inline-block;
  font-size: 1.1em;
}

.itemsContainer li button {
  background-color: #D9534F;
  color: #fff;
  border: none;
  font-size: 1.4em;
  padding: 1px 10px;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
}

.itemsContainer li button:hover {
  background-color: rgba(217, 83, 79, 0.7);
}

.slideOut {
  -webkit-animation: slideOut 300ms forwards;
  animation: slideOut 300ms forwards;
}

@-webkit-keyframes slideOut {
  from {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
  }
  to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
  }
}

@keyframes slideOut {
  from {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
  }
  to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
  }
}

.fadeIn {
  -webkit-animation: fadein 400ms;
  animation: fadein 400ms;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


